import Vue from 'vue'
import VueRouter from 'vue-router'

// 懒加载
const Home = () => import('views/home/Home')
const Mine = () => import('views/mine/Mine')
const Category = () => import('views/category/Category')
const Cart = () => import('views/cart/Cart')
const RunDialog = () => import('views/rundialog/RunDialog')
const Subscribe = () => import('views/subscribe/Subscribe')
const Machine = () => import('views/machine/Machine')


Vue.use(VueRouter)
const routes = [
  {
    path: '',
    redirect: '/home'
  },	
  { 
    path: '/home',
	name:'home',
    // 指定的组件
    component:Home,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'模拟聊天室'
	},
  },	
  { 
    path: '/mine',
	name:'mine',
    // 指定的组件
    component:Mine,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'我的'
	},
  },
  { 
    path: '/category',
	name:'category',
    // 指定的组件
    component:Category,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'通讯录'
	},
  },
  { 
    path: '/rundialog',
	name:'rundialog',
    // 指定的组件
    component:RunDialog,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'对话框'
	},
  },
  { 
    path: '/cart',
	name:'cart',
    // 指定的组件
    component:Cart,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'发现'
	},
  },
  { 
    path: '/subscribe',
	name:'subscribe',
    // 指定的组件
    component:Subscribe,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'预约'
	},
  },
  { 
    path: '/machine',
	name:'machine',
    // 指定的组件
    component:Machine,
	meta: {
		//requireAuth: true, // 判断是否需要登录
		title:'添加机器'
	},
  },
]
const router = new VueRouter({
  routes,
  // url模式
  //mode:'history'
  mode:'hash'
})

export default router
